.Jokelist{
    display: flex;
    width: 80%;
    height: 80%;
}

.Jokelist-sidebar{
    background: #9575cd;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 30%;
    text-align: center;
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1),
    inset 0 0 25px #7e57c2;
}

.Jokelist-title{
    font-size: 4rem;
    color:#FFF;
    margin:2rem;
    font-weight: 300;
    letter-spacing: 0.6rem;
}

.Jokelist-title span{
    font-weight: 700;
    letter-spacing: 0px;
}

.Jokelist-sidebar img {
    width: 50%;
    border-radius: 50%;
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
  }

.Jokelist-jokes {
    height: 90%;
    background: white;
    align-self: center;
    width: 70%;
    overflow: scroll;
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
  }